h1,
.h1 {
  font-size: 48px;
  color: #fff;
  letter-spacing: 2px;
  margin-bottom: 10px;
  @include futura-b;
  @include md {
    font-size: 38px; }
  @include sm {
    font-size: 28px; } }


h5,
.h5 {
  font-size: 18px;
  @include futura-b;
  @include md {
    max-width: 430px;
    margin: 0 auto; }
  @include sm {
    font-size: 14px; } }
