.lang {
  position: relative;
}

.lang:hover .lang__list {
  opacity: 1;
  pointer-events: auto;
}

.lang:hover .lang__icon {
  transform: rotate(180deg);
}

.lang__btn {
  position: relative;
  background: transparent;
  *zoom: 1;
}

.lang__btn:after {
  content: " ";
  display: table;
  clear: both;
}

.lang__pic {
  float: left;
  margin-right: 15px;
  width: 52px;
  height: 52px;
  border-radius: 50%;
  font-size: 20px;
  text-align: center;
  line-height: 56px;
  text-transform: uppercase;
  flex: 1 0 auto;
  color: #fff;
  font-family: "FuturaBold", Arial, Helvetica, sans-serif;
  font-weight: 600;
}

.lang__text {
  overflow: hidden;
  display: block;
  padding: 5px 0;
  text-align: left;
  line-height: 1;
}

.lang__text-main {
  display: inline-block;
  color: #fff;
  font-size: 18px;
  width: 100%;
  font-family: "RobotoMedium", Arial, Helvetica, sans-serif;
  font-weight: 500;
}

.lang__text-secondary {
  color: #5E677E;
  font-size: 16px;
  line-height: 1.6;
}

.lang__icon {
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -5px;
  width: 17px;
  height: 11px;
  background: url("../img/arrow.svg") no-repeat;
  background-size: cover;
  -moz-transition: transform 0.15s ease-in;
  -o-transition: transform 0.15s ease-in;
  -webkit-transition: transform 0.15s ease-in;
  -ms-transition: transform 0.15s ease-in;
  transition: transform 0.15s ease-in;
}

.lang__list {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  right: 100%;
  top: 0;
  z-index: 9;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  background: #2B2839;
}

.lang__list > a {
