}

.steps {
  margin: 50px 0 70px;
  display: flex;
  flex-flow: row nowrap;
  border-radius: 50px;
  box-shadow: 0 2px 96px 0 rgba(0, 0, 0, 0.25);
}

@media only screen and (max-width: 1024px) {
  .steps {
    margin-left: 25%;
    min-width: 940px;
  }
}

@media only screen and (max-width: 767px) {
  .steps {
    margin: 30px 0 35px 28px;
  }
}

.steps__item {
  position: relative;
  padding: 30px 60px 15px;
  text-align: center;
  flex-basis: 33.3%;
  max-width: 33.3%;
  background-image: linear-gradient(to right, #2A2049, #473C78);
}

.steps__item:first-child {
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
}

.steps__item:last-child {
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
}

.steps__item:not(:last-child):after {
  content: '';
  position: absolute;
  z-index: 2;
  right: -27px;
  top: 50%;
  width: 54px;
  height: 54px;
  border-radius: 50%;
  transform: translateY(-50%);
  background-image: linear-gradient(102deg, #31294D, #524588);
}

.steps__item:not(:last-child):before {
  content: '';
  position: absolute;
  z-index: 3;
  right: -5px;
  top: 50%;
  transform: translateY(-50%);
  background: url("../img/arr-right-gradient.svg");
  width: 10px;
  height: 16px;
  background-size: cover;
}

.steps__item.is-active {
  background-image: linear-gradient(to right, #D975FC, #A779FF, #A2CDFF);
  box-shadow: 0 -33px 74px 0 #906BD7;
}

.steps__item.is-active:after {
  background-image: linear-gradient(124deg, #EEBCFF, #A779FF);
}

.steps__item.is-active:before {
  background: url("../img/arr-right-white.svg");
}

.steps__item.is-active .steps__title {
  color: #fff;
}

.steps__pic {
  margin-bottom: 5px;
}

