.footer {
  background: $violet;
  padding: 25px 0;
  margin-top: -80px;
  @include sm {
    padding: 10px 0;
    margin-top: -48px; }
  &__in {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between; }
  &__text {
    color: $violet-light;
    &:not(:last-child) {
      margin-right: 50px; } }
  &__left {
    @include sm {
      font-size: 14px; } }
  &__right {
    > a {
      @include tr(color .3s ease-in);
      &:hover {
        color: $violet-hover; }
      @include sm {
        font-size: 13px;
        display: inline-block;
        vertical-align: top;
        padding-top: 5px; } } } }
