.form-el {
  border-radius: 20px;
  box-shadow: 0 17px 50px 0 rgba(#000, .2); }

.btn {
  display: inline-block;
  vertical-align: middle;
  color: #fff;
  padding: 14px 26px;
  text-align: center;
  background-image: linear-gradient(125deg, #F8906C, #F76FB2);
  font-size: 18px;
  @include futura-b;
  @include sm {
    font-size: 14px;
    padding: 8px 26px; }
  &:not(.btn_nomedia) {
    @include r(1024) {
      width: 100%; } }
  &:hover {
    background-image: linear-gradient(125deg, #F8906C, #FD50A5); }
  &_steam {
    &:before {
      content: '';
      display: inline-block;
      vertical-align: middle;
      width: 54px;
      height: 31px;
      background: url('../img/steam-icon.svg') no-repeat;
      background-size: contain;
      margin-right: 5px;
      @include sm {
        width: 43px;
        height: 25px; } } } }

.input {
  white-space: nowrap;
  overflow: hidden;
  display: flex;
  flex-flow: row nowrap;
  min-height: 59px;
  @include tr(transform .3s ease-in-out);
  @include sm {
    min-height: 41px; }
  input {
    width: 100%;
    padding: 5px 26px;
    background: #fff;
    color: $black;
    border: none;
    font-size: 18px;
    @include tr(background .15s ease-in-out);
    @include sm {
      font-size: 14px; }
    @include placeholder {
      color: $violet-light;
      font-size: 18px;
      @include tr(color .15s ease-in-out);
      @include sm {
        font-size: 11px; } } }
  &:hover {
    transform: scale(1.15, 1.15); }
  &.is-empty {
    input {
      background: linear-gradient(125deg, #31294D, #524588);
      color: $black; }
    &:hover {
      input {
        background: #fff;
        &::placeholder {
          color: transparent; } } } } }

.form {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
  justify-content: space-between;
  max-width: 900px;
  margin: 0 auto;
  @include r(1024) {
    max-width: 575px;
    flex-flow: column nowrap; }
  &__item {
    @include r(1024) {
      width: 100%;
      margin-bottom: 30px; }
    &:first-child {
      max-width: 488px;
      flex-basis: 488px;
      @include r(1024) {
        max-width: 100%;
        flex-basis: auto; } }
    // &:last-child
    //   max-width: 340px
    //   flex-basis: 340px
    &:hover {
      .form__desc {
        opacity: 1;
        @include md {
          display: block; } } }
    &.is-error {
      .form__title {
        color: $orange; }
      .form__error {
        display: block;
        color: $orange; } } }
  &__title {
    text-align: center;
    font-size: 18px;
    margin-bottom: 20px;
    @include futura-b;
    @include sm {
      font-size: 14px; } }
  &__error {
    display: none;
    padding-top: 20px;
    text-align: center; }

  &__desc {
    text-align: center;
    padding: 20px 40px;
    opacity: 0;
    font-size: 18px;
    color: #fff;
    @include tr(opacity .15s ease-in-out);
    @include roboto-r;
    @include md {
      display: none; }
    @include sm {
      font-size: 14px; }
    > a {
      color: $green;
      @include tr(opacity .3s ease-in-out);
      &:hover {
        opacity: .7; } } } }
