//fonts

@include font('FuturaBold', 'FuturaBold');
@include font('RobotoRegular', 'RobotoRegular');
@include font('RobotoMedium', 'RobotoMedium');

@mixin futura-b {
  font-family: "FuturaBold", Arial, Helvetica, sans-serif;
  font-weight: 600; }

@mixin roboto-r {
  font-family: "RobotoRegular", Arial, Helvetica, sans-serif;
  font-weight: 400; }

@mixin roboto-m {
  font-family: "RobotoMedium", Arial, Helvetica, sans-serif;
  font-weight: 500; }

html, body {
  height: 100%; }


.wrapper {
  min-height: 100%;
  &__inside {
    padding-bottom: 80px;
    @include sm {
      padding-bottom: 48px; } } }

body {
  font-size: 20px;
  line-height: 1.4;
  min-width: 330px;
  background: url('../img/bg.jpg') #000 no-repeat;
  background-position: 0 100%;
  color: #fff;
  @include smooth-font;
  @include roboto-r; }

a {
  text-decoration: none; }

button {
  background: transparent; }

.text-center {
  text-align: center; }

.container {
  max-width: 1275px;
  margin: 0 auto;
  padding: 0 40px;
  @include clr;
  @include sm {
    padding: 0 15px; }
  &_sm {
    max-width: 1024px;
    overflow: hidden; } }
