.header {
  background: $violet;
  padding: 24px 0 10px;
  margin-bottom: 90px;
  @include md {
    margin-bottom: 54px; }
  &__in {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between; }
  &__logo {
    margin-right: 100px;
    @include md {
      margin-right: 40px; } }
  &__link {
    color: #fff;
    @include tr(color .3s ease-in-out);
    &:not(:last-child) {
      margin-right: 35px; }
    .icon-question {
      color: $green; }
    .icon-conversation {
      color: $pink; }
    .icon {
      display: inline-block;
      vertical-align: middle;
      width: 30px;
      height: 30px;
      margin-right: 15px; }
    > span {
      display: inline-block;
      vertical-align: middle; } }
  &__link_green {
    &:hover {
      color: $green; } }
  &__link_pink {
    &:hover {
      color: $pink; } } }
